html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

html::after,
html::before {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

body {
  font-family: "Open Sans", Arial, sans-serif;
  margin: 0;
}

ul[class] {
  list-style-type: none;
}

.container {
  max-width: 935px;
  width: 100%;
  margin: 0 auto;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

.img-responsive {
  display: block;
  height: auto;
  max-width: 100%;
  width: 100%;
}

.icon {
  display: inline-block;
}

.icon-close {
  background-image: url(../img/dest/sprite.png);
  background-position: -123px -50px;
  width: 15px;
  height: 15px;
}

.icon-fb {
  background-image: url(../img/dest/sprite.png);
  background-position: 0px 0px;
  width: 41px;
  height: 41px;
}

.icon-inst {
  background-image: url(../img/dest/sprite.png);
  background-position: -41px 0px;
  width: 41px;
  height: 41px;
}

.icon-left {
  background-image: url(../img/dest/sprite.png);
  background-position: -123px 0px;
  width: 29px;
  height: 50px;
}

.icon-pint {
  background-image: url(../img/dest/sprite.png);
  background-position: 0px -41px;
  width: 41px;
  height: 41px;
}

.icon-right {
  background-image: url(../img/dest/sprite.png);
  background-position: 0px -82px;
  width: 29px;
  height: 50px;
}

.icon-tw {
  background-image: url(../img/dest/sprite.png);
  background-position: -41px -41px;
  width: 41px;
  height: 41px;
}

.icon-yt {
  background-image: url(../img/dest/sprite.png);
  background-position: -82px 0px;
  width: 41px;
  height: 41px;
}

.product {
  margin-bottom: 83px;
}

.product__title {
  font-size: 24px;
  font-weight: 700;
  color: #282828;
  font-family: "Oswald", Arial, sans-serif;
  text-transform: uppercase;
  margin: 31px 0 21px;
  padding-left: 38px;
  letter-spacing: .7px;
  line-height: 1.5;
}

.product__content {
  padding: 0 38px;
}

.product__figure {
  margin-bottom: 14px;
}

.product__sku {
  color: #6b6e77;
  font-size: 12px;
  margin-bottom: 10px;
  margin-top: 0;
}

.product__prices {
  margin-bottom: 6px;
}

.product__price {
  font-weight: 700;
  display: inline-block;
  font-size: 36px;
  color: #2c303d;
}

.product__price_old {
  text-decoration: line-through;
}

.product__price_new {
  color: #ce2c52;
}

.product__price + .product__price {
  padding-left: 15px;
}

.product__rating {
  margin-bottom: 30px;
  padding-left: 0;
  margin-top: 0;
}

.product__rating::after {
  content: "";
  display: table;
  clear: both;
}

.product__rating-item {
  color: #ababab;
  font-size: 23px;
  float: left;
  line-height: 18px;
}

.product__rating-item_filled {
  color: #ce2c52;
}

.product__share {
  font-size: 18px;
  color: #2c87ce;
  font-weight: 700;
  cursor: pointer;
  display: none;
}

.product__social-list {
  padding-left: 0;
  margin-bottom: 26px;
}

.product__social-item {
  display: inline-block;
  margin-right: 11px;
}

.product__social-title {
  font-size: 18px;
  color: #2c87ce;
  font-weight: 700;
  margin: 0;
}

.product__social-close {
  border: 0;
  padding: 0;
  background-color: #fff;
  position: absolute;
  right: 15px;
  top: 14px;
}

.product__details-title {
  display: none;
  font-weight: 700;
  text-transform: uppercase;
  color: #2c303d;
  margin-bottom: 7px;
  letter-spacing: .5px;
}

.product__details {
  font-size: 14px;
  color: #282828;
  margin-bottom: 36px;
  line-height: 1.45;
  padding-right: 5px;
  letter-spacing: 0.4px;
  word-spacing: 0.2px;
}

.product__sizes {
  margin-bottom: 20px;
}

.product__sizes-title {
  font-weight: 700;
  text-transform: uppercase;
  color: #2c303d;
  margin-bottom: 15px;
  font-size: 20px;
  text-align: center;
  letter-spacing: 1.2px;
}

.product__sizes-buttons {
  text-align: center;
  max-width: 238px;
  margin: 0 auto;
}

.product__sizes-buttons::after {
  content: "";
  display: table;
  clear: both;
}

.product__sizes-button {
  width: 38px;
  height: 38px;
  position: relative;
  text-align: center;
  cursor: pointer;
  float: left;
  margin-bottom: 10px;
}

.product__sizes-button:not(:last-child) {
  margin-right: 28px;
}

.product__sizes-button input {
  position: absolute;
  height: 0;
  width: 0;
}

.product__sizes-button span {
  display: inline-block;
  width: 100%;
  height: 100%;
  background-color: #fff;
  border: 1px solid #2c303d;
  display: inline-block;
  color: #2c303d;
  text-align: center;
  vertical-align: middle;
  text-transform: uppercase;
  line-height: 38px;
  font-size: 9px;
  border-radius: 50%;
}

.product__buy-btn {
  font-size: 18px;
  font-weight: 700;
  color: #2c303d;
  text-transform: uppercase;
  text-align: center;
  border: 0;
  padding: 0;
  background-color: #8ae6cf;
  display: block;
  width: 100%;
  padding: 18px 0;
  max-width: 280px;
  margin: 0 auto;
  border-radius: 30px;
}

.product__sizes-button input:checked ~ span {
  background-color: #2c303d;
  color: #fff;
}

.products::after {
  content: "";
  display: table;
  clear: both;
}

.products__title {
  font-size: 24px;
  font-weight: 700;
  color: #2c303d;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 1.2px;
  margin-bottom: 18px;
}

.products__container {
  position: relative;
  max-width: 265px;
  margin: 0 auto;
}

.products__nav {
  position: relative;
  max-width: 340px;
  margin: 0 auto;
}

.products__nav-arrow {
  background-color: transparent;
  margin: 0;
  border: 0;
  padding: 0;
  overflow: hidden;
  width: 29px;
  height: 50px;
  position: absolute;
  top: 107px;
}

.products__nav-arrow_prev {
  left: 0;
}

.products__nav-arrow_next {
  right: 0;
}

.product-similar {
  max-width: 265px;
  margin: 0 auto;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 0;
  opacity: 0;
  -webkit-transition: all .3s ease-in-out;
  -o-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out;
}

.product-similar.active {
  z-index: 1;
  opacity: 1;
}

.product-similar__figure {
  position: relative;
}

.product-similar__prices {
  text-align: center;
  position: absolute;
  width: 100%;
  left: 0;
  bottom: 0;
  background-color: #8ae6cf;
  filter: alpha(opacity=80);
  background-color: rgba(138, 230, 207, 0.8);
}

.product-similar__price {
  display: inline-block;
  font-size: 18px;
  font-weight: 700;
  color: #2c303d;
}

.product-similar__price_new {
  color: #ce2c52;
}

.product-similar__price_old {
  display: none;
}

.unactive {
  display: none;
}

