    -o-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
  }

  .product__social-inner::before {
    content: '';
    display: block;
    left: 50%;
    position: absolute;
    top: -14px;
    margin-left: -5px;
    z-index: 0;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 8px 13.9px 8px;
    border-color: transparent transparent #ffffff transparent;
    -webkit-filter: drop-shadow(0px -1px 1px rgba(0, 0, 0, 0.75));
    filter: drop-shadow(0px -1px 1px rgba(0, 0, 0, 0.75));
  }

  .product__social-inner::after {
    content: '';
    display: block;
    left: 50%;
    position: absolute;
    top: -12px;
    margin-left: -5px;
    z-index: 1;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 8px 13.9px 8px;
    border-color: transparent transparent #ffffff transparent;
  }

  .product__social-inner.modal-open {
    visibility: visible;
    opacity: 1;
    z-index: 9;
  }

  .product__social-list {
    margin-top: 14px;
  }

  .product__social-item {
    display: inline-block;
    margin-bottom: 0;
  }

  .product__social-item + .product__social-item {
    margin-left: 9px;
  }

  .product__details {
    margin-top: 23px;
    margin-bottom: 28px;
  }

  .product__sizes {
    margin-bottom: 17px;
  }

  .product__sizes-title {
    display: inline-block;
    vertical-align: middle;
    margin-top: 0;
  }

  .product__sizes-buttons {
    display: inline-block;
    vertical-align: middle;
    margin-left: 12px;
  }

  .product__sizes-button {
    width: 36px;
    height: 36px;
  }

  .product__sizes-button:not(:last-child) {
    margin-right: 15px;
  }

  .products {
    margin: 0 -10px;
  }

  .products__title {
    margin-left: 0;
    text-align: left;
    padding-left: 15px;
    margin-bottom: 16px;
  }

  .product-similar {
    max-width: 33.333%;
    padding: 0 10px;
    margin-bottom: 20px;
  }

  .product-similar__prices {
    background-color: #8ae6cf;
    width: auto;
    padding: 0;
    bottom: 22px;
    right: 0;
    left: auto;
  }

  .product-similar__price {
    font-size: 16px;
    padding: 2px 8px;
  }

  .product-similar__price_old {
    display: inline-block;
  }
}

@media screen and (max-width: 933px) {
  .mobile-hidden {
    display: none;
  }
